import { 
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    SIGNUP_SUCCESS,
    SIGNUP_FAIL,
    LOAD_USER_FAIL,
    LOAD_USER_SUCCESS,
    IS_LOADING

 } from "../Actions/types";


 const initialState = {
    access: localStorage.getItem('access'),
    refresh: localStorage.getItem('refresh'),
    isAuthenticated : false,
    user: null,
    loading: false,
    success: false,
 }


export default function(state=initialState, action){
    const {type, payload} = action

    switch(type){

        case IS_LOADING:
            return{
                ...state,
                loading: true
            }
        case SIGNUP_SUCCESS:
            return{
                ...state,
                success: true,
                isAuthenticated: false,
                loading: false,
            }

        case SIGNUP_FAIL:
            return{
                ...state,
                success: false,
                isAuthenticated: false,
                loading: false
            }

        case LOGIN_SUCCESS:
            localStorage.setItem('access', payload.access)
            return{
                ...state,
                isAuthenticated: true,
                refresh: payload.refresh,
                access: payload.access,
                success: true
            }

         case LOGIN_FAIL:
            localStorage.removeItem('access')
            localStorage.removeItem('refresh')
            return{
                ...state,
                access: '',
                refresh: '',
                isAuthenticated: false,
                loading: false
            }


        case LOAD_USER_FAIL:
            return{
                ...state,
                user: null,
            }

        case LOAD_USER_SUCCESS:
            return{
                ...state,
                user: payload,
                loading: false,
            }

        

        default:
            return state;
    }
}
