import axios from "axios";
import { 
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    SIGNUP_SUCCESS,
    SIGNUP_FAIL,
    LOAD_USER_FAIL,
    LOAD_USER_SUCCESS,
    IS_LOADING,

 } from "../Actions/types";


const loadUser = () => async dispatch =>{
    if(localStorage.getItem('access')){

    const config = {
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `JWT ${localStorage.getItem('access')}`,
            'Accept': 'application/json'
        }
    }
    try{
        const res = await axios.get(`${process.env.REACT_APP_ENDPOINT_URL}/auth/users/me/`, config)

        if(res.data){
        dispatch({
            type: LOAD_USER_SUCCESS,
        })

        }else{
        dispatch({
            type: LOAD_USER_FAIL,
        })
        }

    } catch(error){
        dispatch({
            payload: LOGIN_FAIL
        })
    }
    }
}


// LOGIN FUNCTION
export const login = (email, password) => async dispatch => {

    dispatch({
        type: IS_LOADING
    })

    const config = {
        headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'withCrendentials': true
        }
    }

    const body = JSON.stringify({email, password})

    try{
        const res = await axios.post(`${process.env.REACT_APP_ENDPOINT_URL}/auth/jwt/create/`, body, config)

        if(res.data){

        dispatch({
            type: LOGIN_SUCCESS,
        })
        // load user after login
        dispatch(loadUser())

        }else{

        dispatch({
            type:LOGIN_FAIL,
        })
        }
    } catch(error){
        dispatch({
            type: LOGIN_FAIL
        })
    }
 }



 // SIGNUP FUNCTION
export const sign_up = (name, email, phone, password, re_password) => async dispatch => {

    dispatch({
        type: IS_LOADING
    })

    const config = {
        headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'withCrendentials': true
        }
    }

    const body = JSON.stringify({name, email, phone, password, re_password})

    try{
        const res = await axios.post(`${process.env.REACT_APP_ENDPOINT_URL}/account/sign-up/`, body, config)

        if(res.data){

        dispatch({
            type: SIGNUP_SUCCESS,
        })

        }else{

        dispatch({
            type: SIGNUP_FAIL,
        })
        }
    } catch(error){
        dispatch({
            type: SIGNUP_FAIL
        })
    }
 }